.footer{
    background-color:#0fdad6;
    display:flex;
    justify-content: center;
    padding: 5px;
}
.footer__text{
    color:black;
    font-size: 17px;
    font-weight:500;
    margin-right:40px;
}
.footer__icon {
    padding: 0px 10px;
}

a{
  margin-top:0px !important;
  background-color:#0fdad6 !important;
  border: 0px solid aqua;
  color: black !important;
  padding: 0px 0px;
  text-decoration: none ;
  font-weight:600;
}
a:hover{
    transform: scale(1.9) ease-in;
    background-color: #0fdad6 !important;
    /* background-color: white !important; */
    color:rgb(50, 41, 213) !important;
    cursor:pointer;
}
